.payment-simulate-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 80vh;
    padding: 20px;
    background-color: #f4f7f6;
    font-family: Arial, sans-serif;
    color: #333;
}

.payment-simulate-container h2 {
    color: #0056b3;
    margin-bottom: 25px;
    font-size: 2.2em;
}

.payment-content {
    background-color: #ffffff;
    padding: 40px 30px;
    border-radius: 10px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    text-align: center;
    max-width: 500px;
    width: 100%;
}

.payment-content p {
    font-size: 1.1em;
    margin-bottom: 15px;
    line-height: 1.6;
}

.payment-content h3 {
    font-size: 2.5em;
    color: #28a745; /* Green for amount */
    margin-bottom: 30px;
}

.payment-button {
    background-color: #007bff;
    color: white;
    padding: 12px 25px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1.1em;
    transition: background-color 0.3s ease;
}

.payment-button:hover {
    background-color: #0056b3;
}

.payment-button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
}

.success-message {
    color: #28a745;
    font-weight: bold;
    font-size: 1.3em;
    margin-bottom: 20px;
}

.processing-message {
    color: #ffc107;
    font-weight: bold;
    font-size: 1.3em;
    margin-bottom: 20px;
}

.error-message {
    color: #dc3545;
    font-weight: bold;
    font-size: 1.3em;
    margin-bottom: 20px;
}`, "",{"version":3,"sources":["webpack://./src/pages/Payment/PaymentSimulatePage.css"],"names":[],"mappings":"AAAA,yDAAyD;;AAEzD;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,gBAAgB;IAChB,aAAa;IACb,yBAAyB;IACzB,8BAA8B;IAC9B,WAAW;AACf;;AAEA;IACI,cAAc;IACd,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;IACzB,kBAAkB;IAClB,mBAAmB;IACnB,yCAAyC;IACzC,kBAAkB;IAClB,gBAAgB;IAChB,WAAW;AACf;;AAEA;IACI,gBAAgB;IAChB,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;IAChB,cAAc,EAAE,qBAAqB;IACrC,mBAAmB;AACvB;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,kBAAkB;IAClB,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,sCAAsC;AAC1C;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;IACzB,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,iBAAiB;IACjB,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,iBAAiB;IACjB,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,iBAAiB;IACjB,gBAAgB;IAChB,mBAAmB;AACvB","sourcesContent":["/* src/pages/Payment/PaymentSimulatePage.css (New File) */\n\n.payment-simulate-container {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    min-height: 80vh;\n    padding: 20px;\n    background-color: #f4f7f6;\n    font-family: Arial, sans-serif;\n    color: #333;\n}\n\n.payment-simulate-container h2 {\n    color: #0056b3;\n    margin-bottom: 25px;\n    font-size: 2.2em;\n}\n\n.payment-content {\n    background-color: #ffffff;\n    padding: 40px 30px;\n    border-radius: 10px;\n    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);\n    text-align: center;\n    max-width: 500px;\n    width: 100%;\n}\n\n.payment-content p {\n    font-size: 1.1em;\n    margin-bottom: 15px;\n    line-height: 1.6;\n}\n\n.payment-content h3 {\n    font-size: 2.5em;\n    color: #28a745; /* Green for amount */\n    margin-bottom: 30px;\n}\n\n.payment-button {\n    background-color: #007bff;\n    color: white;\n    padding: 12px 25px;\n    border: none;\n    border-radius: 5px;\n    cursor: pointer;\n    font-size: 1.1em;\n    transition: background-color 0.3s ease;\n}\n\n.payment-button: