import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';

import './PaymentSimulatePage.css'; // Create this CSS file for styling

const PaymentSimulatePage = () => {
    const location = useLocation();
    const navigate = useNavigate();
    const [orderId, setOrderId] = useState(null);
    const [amount, setAmount] = useState(null);
    const [paymentStatus, setPaymentStatus] = useState('pending'); // 'pending', 'processing', 'completed', 'failed'

    useEffect(() => {
        // Parse query parameters from the URL
        const params = new URLSearchParams(location.search);
        const id = params.get('orderId');
        const amt = params.get('amount');

        if (id && amt) {
            setOrderId(id);
            setAmount(parseFloat(amt).toFixed(2));
        } else {
            // Redirect or show error if parameters are missing
            setPaymentStatus('failed');
            console.error('Missing orderId or amount in URL parameters.');
        }
    }, [location.search]);

    const handleSimulatePayment = () => {
        setPaymentStatus('processing');
        // Simulate a delay for payment processing
        setTimeout(() => {
            setPaymentStatus('completed');
            // Optionally, you can redirect the user back to their orders page
            // after a short delay or a button click.
            // navigate('/patient/orders');
        }, 2000); // 2-second delay
    };

    const handleGoBack = () => {
        navigate('/patient/orders'); // Navigate back to the patient's orders page
    };

    let content;
    if (!orderId || !amount) {
        content = <p className="error-message">Invalid payment request. Please try again from your orders page.</p>;
    } else if (paymentStatus === 'pending') {
        content = (
            <>
                <p>You are about to pay for Order #...{orderId.substring(orderId.length - 6)}</p>
                <h3>Amount: ${amount}</h3>
                <button className="payment-button" onClick={handleSimulatePayment}>Simulate Payment</button>
            </>
        );
    } else if (paymentStatus === 'processing') {
        content = <p className="processing-message">Processing your payment...</p>;
    } else if (paymentStatus === 'completed') {
        content = (
            <>
                <p className="success-message">Payment Simulated Successfully!</p>
                <p>Order #...{orderId.substring(orderId.length - 6)} for ${amount} has been processed.</p>
                <button className="payment-button" onClick={handleGoBack}>Go back to My Orders</button>
            </>
        );
    } else if (paymentStatus === 'failed') {
        content = (
            <>
                <p className="error-message">Payment Simulation Failed.</p>
                <p>There was an issue processing your payment. Please try again.</p>
                <button className="payment-button" onClick={handleGoBack}>Go back to My Orders</button>
            </>
        );
    }

    return (
        <div className="payment-simulate-container">
            <h2>Payment Gateway Simulation</h2>
            <div className="payment-content">
                {content}
            </div>
        </div>
    );
};

export default PaymentSimulatePage;